.map-creator-wrapper {
  display: flex;
  // align-items: center;
  justify-content: center;
  height: calc(100vh - 4rem);
  .map-creator-container {
    margin-top: 8rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    min-width: 30rem;
    max-width: 40rem;
    padding: 2rem;
    flex-direction: column;
    row-gap: 1rem;
    height: 15rem;
    // border: solid 1px black;
    border: none;
    background-color: #f2f2f2;
    border-radius: 10px;
    section {
      background-color: white;
      width: -webkit-fill-available;
      input {
        width: 100%;
      }
    }
    .progress-div {
      background: transparent;
      display: flex;
      flex-direction: column;
      row-gap: 0.4rem;
      align-items: center;
      span {
        font-style: italic;
        font-size: 0.8rem;
        color: rgba(0, 0, 0, 0.198);
      }
    }
    button {
      background-color: white;
      width: auto;
    }
    .error-span {
      font-weight: bold;
      color: red;
      font-size: 0.8rem;
    }
  }
}
