/* Global */
:root {
  --map-border: 2px solid #505d79;
  --map-border-radius: 10px;
  --map-input-button-box-height: 2rem;
  --map-button-color: #bbbbbb;
  --map-button-border-radius: 5px;
}

input {
  border: var(--map-border);
  border-radius: var(--map-border-radius);
  height: var(--map-input-button-box-height);
}

button {
  border: 3px solid var(--map-button-color);
  border-radius: var(--map-button-border-radius);
  padding: 0.825rem;
  font-size: 1.1rem;
}

button:hover {
  background-color: #005086;
  color: #ffffff;
}

input[type="file"] {
  border: none;
  border-radius: 0px;
}

/* Global  */

.map-editor-div {
  display: grid;
  grid-template-areas: "mtv mti";
  column-gap: 1rem;
  grid-template-columns: 5fr 2fr;
}
.input-div {
  grid-area: mti;
  min-width: 15rem;
  background-color: #f6f6f6;
  height: calc(100vh - 4rem);
  /* border: solid darkgray; */
  overflow: auto;
}
.map-div {
  margin: 1rem;
  grid-area: mtv;
  display: grid;
  grid-template-areas: "tools map-div";
  grid-template-columns: auto 95%; /* 1fr 5fr */
}
.konva-container {
  position: relative;
  display: flex;
  justify-content: flex-end;
  align-items: flex-start;
  padding: 0.2rem;
}
#konva-div-ruler {
  position: absolute;
}
#konva-div {
  background: white;
  border: solid black 1px;
  position: absolute;
  border-radius: 8px;
  overflow: hidden
}
.map-editor-button {
  padding: 1rem;
  border: none;
  background-color: var(--map-button-color);
  font-size: 1.1rem;
}
.download-button {
  padding: 1rem;
  border: none;
  background-color: lightgray;
  box-shadow: 0 0 10px 0px rgba(0, 0, 0, 0.356);
}
.download-button:disabled {
  color: darkgray;
}
.map-editor-button:focus {
  border: solid grey 2px;
}
.map-editor-rounded-button:focus {
  background-color: #005086b7;
}
.map-editor-rounded-button:hover {
  background-color: #005086b7;
}
.map-editor-rounded-button {
  border: none;
  border-radius: 50%;
  width: 3rem;
  height: 3rem;
  margin-left: 1rem;
  background-color: var(--map-button-color);
}
.inplace-clockwise {
  background-color: var(--map-button-color);
  margin-left: 1rem;
  font-size: 1.1rem;
  width: 140px;
  border: none;
}
.tab-buttons {
  display: flex;
  justify-content: space-around;
  align-items: center;
  margin: 1.5em 0.5rem;
  column-gap: 0.2rem;
}
.tab-buttons > button {
  flex: 1;
  text-align: center;
  border: var(--map-button-border-radius);
  border-radius: var(--map-button-border-radius);
  /* overflow: hidden; */
  white-space: nowrap;
}
.tab-content {
  margin-top: 1rem;
  margin-bottom: 1rem;
}
.map-editor-button-active {
  padding: 1rem;
  margin: 0.2rem;
  border: none;
  background-color: navy;
  color: white;
}
.routes-drop-down {
  width: 90%;
  margin-left: 0.75rem;
  background-clip: padding-box;
  padding: 1rem 0;
  border: var(--map-border);
  border-radius: var(--map-button-border-radius);
}
.map-side-column {
  display: flex;
  flex-direction: column;
  margin-right: 1rem;
  margin-left: 1rem;
  row-gap: 1rem;
}
.input-form {
  display: flex;
  flex-direction: column;
  padding: 1rem;
  border-radius: var(--map-border-radius);
  background-color: #b8afaf6b;
}
.input-form > * {
  margin-top: 0.5rem;
  margin-bottom: 0.5rem;
}

.edit-mode-button {
  border: none;
  padding: 1rem;
  font-size: 1.1rem;
  /* box-shadow: 0 0 10px 0px rgba(0, 0, 0, 0.356); */
  background-color: var(--map-button-color);
}
.align-map-input-form {
  display: flex;
}
.align-map-input-form-section {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
}
.align-map-input-form-section > label {
  align-self: flex-start;
  margin-left: 2em;
}
.align-map-input {
  display: flex;
  justify-content: center;
}
.align-map-input p {
  margin-top: 0.6rem;
  margin-left: 0.2rem;
}
.step-size-label {
  align-self: center;
}
.step-size-input {
  width: 35%;
  margin-left: 0.75rem;
}
.total-rotation-input {
  margin-left: 0.25rem;
  padding-left: 0.5rem;
}
.map-editor-rounded-button-div {
  width: 50%;
  display: flex;
  justify-content: space-evenly;
  align-items: flex-start;
}
.map-editor-button-tabs {
  padding: 1em;
  background-color: var(--map-button-color);
}
.map-editor-button-tabs-active {
  padding: 1em;
  background-color: #005086;
  color: white;
}
.station-angle-input {
  width: 65%;
  margin-left: 0.25rem;
  padding-left: 0.5rem;
}

.button-row {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  column-gap: 0.5rem;
}
.add-station-form input {
  margin: 0.5rem;
}
.add-station-form button {
  margin: 0.5rem;
}
.form-checkbox-input {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  margin-top: 0.5rem;
  margin-bottom: 0.5rem;
}
.form-checkbox-input > input {
  margin-right: 1rem;
  margin-left: 0.3rem;
}
.coord-input {
}

