/* Map Editor Upload */
.file-upload-main-container {
  display: flex;
  flex-direction: column;
  margin-right: auto;
  margin-left: auto;
  justify-content: center;
  height: calc(100vh - 4rem);
  padding-inline: 25%;
  row-gap: 0.3rem;
}

.directory-upload-container {
  min-width: 10rem;
  height: 50vh;
  background-color: #f6f6f6;
  border: 1px dashed #333;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: left;
  row-gap: 1rem;
  border-radius: 10px;
  text-align: left;
  padding-inline: 4rem;
}

/* Input of type file */
/* ::-webkit-file-upload-button {
  background: #005086;
  color:#333;
  padding: 2em;
} */

.file-upload-text-button {
  background-color: transparent !important;
  outline: none;
  border: none;
  cursor: pointer;
  text-decoration: underline;
  color: #495464;
  align-self: flex-start;
}

input[type="file"]::file-selector-button {
  padding: 0.5em 0.6em;
  background-color: lightgrey;
  color: #333;
  transition: 1s;
  cursor: pointer;
  border: none;
  text-align: left;
  border-radius: 5px;
}

input[type="file"]::file-selector-button:hover {
  background-color: #a6a9b6;
  color: black;
}

.file-upload-text-button:hover {
  color: #0f0f0f;
}

.load-map-editor-button {
  padding: 0.5em 0.6em;
  border: 0.5px solid lightgray;
}

.load-map-editor-button:enabled {
  background-color: lightgrey;
  color: #333;
  border: none;
  cursor: pointer;
}

.load-map-editor-button:enabled:hover {
  background-color: #a6a9b6;
  color: #0f0f0f;
}

button {
  cursor: pointer;
}

select {
  background-color: #fff;
}

/* Map Editor API */
.map-editor-api-main-container {
  height: calc(100vh - 4rem);
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: auto;
}

.show-maps-container {
  width: 50%;
  max-height: calc(100vh - 5rem);
  display: flex;
  justify-content: space-evenly;
  flex-wrap: wrap;
  column-gap: 1rem;
  row-gap: 0.5rem;
}

.directory-svg {
  padding: 0.25rem;
  cursor: pointer;
  display: flex;
  flex-direction: column;
}
.directory-svg:hover {
  background-color: #e6e6e6;
  border-radius: 5px;
}

.directory-svg-text {
  color: grey;
  cursor: auto;
}

.upload-button {
  display: flex;
  justify-content: center;
  align-items: center;
}
.tools {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  row-gap: 0.5rem;
  min-width: 4rem;
}

.tools > button {
  width: 100%;
}

.commom-dispatch-container {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  row-gap: 0.825rem;
}

.commom-dispatch-container > input {
  align-self: center;
}
.grid-button {
  font-size: 1rem;
}

.station-angle-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  row-gap: 0.5rem;
  font-weight: 400;
}

.sherpa-rotation-text-container {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  text-align: left;
}

.sherpa-rotation-text-container:nth-child(1){
  font-size: 1rem;
  text-align: left;
}
.text-danger {
  color: red;
}
.direct-map-editor-button {
  position: absolute;
   bottom: 10px; 
   right: 10px; 
   white-space: nowrap; 
   font-size: 0.7rem;
}
.station-tag-text {
  color: darkslategrey;
}
.station-tags-input {
  width: 65%;
  margin-left: 0.25rem;
  padding-left: 0.5rem;
}
.station-tags-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  row-gap: 0.5rem;
  font-weight: 400;
  margin-bottom: 0.25rem;
}