.remote-drive-container {
  position: relative;
  height: calc(100vh - 5rem);
  width: 100%;
  .video-wrapper {
    width: 100%;
    height: 100%;
    background-color: black;
  }
  .joystick-container {
    margin: 1rem;
    position: absolute;
    bottom: 0;
    left: 0;
    height: 15rem;
    width: 15rem;
    background-color: transparent;
  }
  .lidar-view-container {
    margin: 1rem;
    position: absolute;
    bottom: 0;
    right: 0;
    height: 15rem;
    width: 15rem;
    border-radius: 50%;
    background-color: whitesmoke;
    display: grid;
    align-items: center;
  }
}
