.status-app-wrapper {
  display: flex;
  flex-direction: column;
  row-gap: 1rem;
  padding-inline: 1rem;
  padding-block: 2rem;
  .status-text {
    display: flex;
    justify-self: flex-start;
    font-weight: 100;
    font-style: italic;
    height: 1.5rem;
  }
  .buttons-wrapper {
    width: calc(100vw-2rem);
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    column-gap: 0.5rem;
    row-gap: 0.5rem;
    .status-button {
      width: calc((100vw - 2rem - 1.5rem) / 4);
      min-width: 8rem;
      min-height: max-content;
      height: 6rem;
      border: none;
      font-size: 1rem;
      text-align: left;
      .spinner-div {
        display: flex;
        justify-content: flex-start;
        align-items: center;
      }
    }
    .status-button:hover {
      color: currentColor;
    }
    .status-button:disabled {
      background: lightgray;
      color: #AAAAAA;
      cursor: not-allowed;
    }
    .desaturated-orange {
      background: #fff3b2;
    }
    .desaturated-yellow {
      background: #fff3b2;
    }
    .desaturated-green {
      background: #a3ddcb;
    }
    .desaturated-red {
      background: #ff7171;
      color: white;
    }
  }
  .stations-wrapper {
    display: flex;
    row-gap: 1rem;
    flex-direction: column;
    min-width: 40rem;
    max-width: 50rem;
    align-content: center;
    min-height: 30vh;
    padding: 5rem 2rem;
    background-color: #f2f2f2;
    border-radius: 5px;
    box-shadow: -2px 2px 4px rgba(0, 0, 0, 0.11),
      1px 0px 2px rgba(0, 0, 0, 0.11);
    .stations-container {
      display: flex;
      justify-content: flex-start;
      align-items: center;
      align-content: center;
      flex-wrap: wrap;
      column-gap: 0.5rem;
      row-gap: 0.5rem;
      .station-button {
        margin: 0;
        cursor: pointer;
        padding-block: 1rem;
        font-size: 0.8rem;
        background-color: lightgrey;
        color: #333;
        min-width: 8rem;
        min-height: max-content;
        border: none;
        font-size: 1rem;
        text-align: left;
        &:hover {
          background: #333;
          color: white;
        }
      }
    }
  }
  .spinner-container {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
  .text-placeholder-wrapper {
    width: 40%;
  }
  .loading-station-button {
    cursor: wait;
  }
}
