.navbar {
  display: grid;
  position: relative;
  grid-template-areas: "nav-links";
  /* grid-template-columns: 1fr 7fr; */
  align-items: center;
  min-height: 4rem;
  padding-left: 1rem;
  padding-right: 1rem;
  box-shadow: 0rem 0.01rem 1rem rgba(0, 0, 0, 0.116);
}

.logo {
  grid-area: logo;
  height: 3rem;
  position: absolute;
}

.nav-links {
  margin-bottom: 0px;
  padding: 0;
  grid-area: nav-links;
  list-style-type: none;
  /* flex-wrap: wrap; */
  column-gap: 10vw;
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.nav-link {
  display: block;
  margin-top: 0.2rem;
  margin-bottom: 0.2rem;
  margin-left: 0.5rem;
  margin-right: 0.5rem;
  text-decoration: none;
  color: grey;
}

.nav-link-active, .logout-link:active {
  color: #414141;
  font-weight: bold;
}

.emergency-stop-button {
  background-color: red;
  color: white;
  padding: 0.5rem;
  border-radius: 2px;
  font-weight: 700;
  border: none;
}

.utility-icons {
  grid-area: utility-icons;
  /* flex-wrap: wrap; */
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}
.utility-icon {
  margin-left: 0.3rem;
  display: flex;
  height: 1.5rem;
}

.konvajs-content {
  cursor: pointer;
}

.nav-link:hover {
  color: #414141;
  cursor: pointer;
}

.logout-link {
  position: absolute;
  right: 0;
}